nav.navbar {
  --local: var(--success-200);
  width: 100%;
  position: fixed;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 75px;
  background-color: white;
  box-shadow: 6px 0px 21px 1px #4f4f4f;
  padding: 0 1em;
  z-index: 2;
}

.hamburger {
  padding: 15px 15px;
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, -webkit-filter;
  transition-property: opacity, filter;
  transition-property: opacity, filter, -webkit-filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible;
}
.hamburger:hover {
  opacity: 0.7;
  background-color: transparent;
}
.hamburger.is-active:hover {
  opacity: 0.7;
}
.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner::before,
.hamburger.is-active .hamburger-inner::after {
  background-color: #000;
}

.hamburger-box {
  width: 40px;
  height: 24px;
  display: inline-block;
  position: relative;
}

.hamburger-inner {
  display: block;
  top: 50%;
  margin-top: -2px;
}
.hamburger-inner,
.hamburger-inner::before,
.hamburger-inner::after {
  width: 40px;
  height: 4px;
  background-color: #000;
  border-radius: 4px;
  position: absolute;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}
.hamburger-inner::before,
.hamburger-inner::after {
  content: "";
  display: block;
}
.hamburger-inner::before {
  top: -10px;
}
.hamburger-inner::after {
  bottom: -10px;
}

.hamburger--squeeze .hamburger-inner {
  transition-duration: 0.075s;
  transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
}
.hamburger--squeeze .hamburger-inner::before {
  transition: top 0.075s 0.12s ease, opacity 0.075s ease;
}
.hamburger--squeeze .hamburger-inner::after {
  transition: bottom 0.075s 0.12s ease,
    -webkit-transform 0.075s cubic-bezier(0.55, 0.055, 0.675, 0.19);
  transition: bottom 0.075s 0.12s ease,
    transform 0.075s cubic-bezier(0.55, 0.055, 0.675, 0.19);
  transition: bottom 0.075s 0.12s ease,
    transform 0.075s cubic-bezier(0.55, 0.055, 0.675, 0.19),
    -webkit-transform 0.075s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.hamburger--squeeze.is-active .hamburger-inner {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  transition-delay: 0.12s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}
.hamburger--squeeze.is-active .hamburger-inner::before {
  top: 0;
  opacity: 0;
  transition: top 0.075s ease, opacity 0.075s 0.12s ease;
}
.hamburger--squeeze.is-active .hamburger-inner::after {
  bottom: 0;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  transition: bottom 0.075s ease,
    -webkit-transform 0.075s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: bottom 0.075s ease,
    transform 0.075s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: bottom 0.075s ease,
    transform 0.075s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1),
    -webkit-transform 0.075s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.nav-mobile.nav-mobile {
  font-size: 0.8em;
  border-radius: 0.8em;
  padding: 0.5em;
}

.nav-buttons a:first-child {
  margin-right: 1em;
}

@media (min-width: 500px) {
  .nav-mobile.nav-mobile {
    padding: 0.6em;
    border-radius: 30px;
    font-size: var(--paragraph-small);
  }
}

.nav-toggle {
  position: fixed;
  height: 100vh;
  width: 300px;
  background: var(--light);
  top: 0;
  left: 0;
}

.nav-toggle ul {
  margin-top: 100px;
  padding: 0;
}

.nav-toggle li {
  list-style-type: none;
  font-size: 1.6em;
}

.nav-toggle a {
  display: block;
  width: 100%;
  height: 100%;
  text-decoration: none;
  color: var(--dark);
  padding: 0.7em 0 0.7em 1.5em;
}

.nav-toggle a:hover,
.nav-toggle a.activelink {
  background: var(--primary-100);
  color: var(--light);
}

.nav-toggle .list-icon {
  margin-right: 0.4em;
}

.message {
  position: absolute;
  top: 3vh;
  left: 10vw;
  right: 10vw;
  border: 3px solid black;
  padding: 0 1em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 10px 13px -7px #000000,
    5px 5px 15px 5px rgba(119, 119, 119, 0);
}

.message.error {
  border: 3px solid var(--danger-100);
  background: var(--danger-300);
}

.message.success {
  border: 3px solid var(--success-100);
  background: var(--success-300);
}

.message svg {
  cursor: pointer;
  z-index: 100;
}

.message svg:hover {
  color: white;
}

.message a {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

